












































































































































































































































































































































































.idm-module-alink{
    color: #1890ff;
    text-decoration: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    transition: color .3s;
    &:hover {
        color: #40a9ff
    }

    &:active {
        color: #096dd9
    }

    &:active,a:hover {
        text-decoration: none;
        outline: 0
    }

    &[disabled] {
        color: rgba(0,0,0,.25);
        cursor: not-allowed;
        pointer-events: none
    }
    &.ellipsis{
      display: inline-block;
      white-space: nowrap;
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
    }
}
