























































































































































































































































































































































































































































































































.idm-text {
  overflow: auto;
  word-wrap: break-word;

  div.ellipsis {
    overflow: hidden;
    white-space: normal;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    line-clamp: 3;
    -webkit-line-clamp: 3; //显示几行
  }

  &::-webkit-scrollbar-track-piece {
    background-color: #ffffff;
  }

  &::-webkit-scrollbar {
    width: 8px;
    height: 9px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #ebebeb;
    background-clip: padding-box;
    min-height: 28px;
    border-radius: 4px;
  }
}
